.1);\n  overflow: hidden;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .chatMsgText {\n  /*文本消息*/\n  padding: .26rem;\n  font-size: .3rem;\n  font-family: 'PingFang-SC-Medium';\n  margin: 0;\n  max-width: 5rem;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent {\n  /*商品消息*/\n  padding: .15rem;\n  display: flex;\n  justify-content: space-between;\n  height: 1.6rem;\n  width: 5rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentPic {\n  height: 1.3rem;\n  width: 1.3rem;\n  border-radius: .1rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentPic img {\n  height: 100%;\n  width: 100%;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentInfo {\n  width: 3.2rem;\n  padding: .05rem 0;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentInfo .goodsContentName {\n  font-size: .29rem;\n  color: #333;\n  font-weight: 400;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentInfo .goodsContentBottom {\n  font-size: .2rem;\n  color: #F61027;\n  font-weight: 600;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentInfo .goodsContentBottom .goodsContentPrice {\n  font-size: .36rem;\n  font-weight: 800;\n  color: #F61027;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .goodsContent .goodsContentInfo .goodsContentBottom .goodsContentProm {\n  color: #ccc;\n  font-size: .2rem;\n  font-weight: 400;\n  margin-left: .08rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .img {\n  /*图片消息*/\n  /*height: 100%;*/\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .img .heightImg {\n  height: 4.2rem;\n  display: block;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .img .widthImg {\n  width: 4.2rem;\n  display: block;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .img .autoImg {\n  height: auto;\n  width: auto;\n  display: block;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .autoReply {\n  width: 4.3rem;\n  padding: .1rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .autoReply .autoReplyTitle {\n  padding: .3rem 0 .2rem .1rem;\n  font-size: .3rem;\n  color: #333;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .chatMsgBox .autoReply .autoReplyItems .autoReplyItem {\n  border-top: .01rem solid #eee;\n  padding: .3rem 0 .3rem .1rem;\n  color: #3695FF;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .loadingImg {\n  display: flex;\n  align-items: center;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .loadingImg img {\n  height: .36rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .chatMsg .whiteBackground {\n  background-color: #fff !important;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink {\n  /* 商品链接*/\n  background-color: #fff;\n  display: flex;\n  max-width: 8rem;\n  padding: 0.133333rem 0.266667rem;\n  justify-content: flex-start;\n  margin-left: 0.533333rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsPic {\n  width: 1.4rem;\n  height: 1.4rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsPic img {\n  height: 100%;\n  width: 100%;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo {\n  width: 5.5rem;\n  padding: .1rem 0;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsName {\n  font-size: .26rem;\n  font-weight: 600;\n  line-height: .32rem;\n  color: #333;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsInfoBottom {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-end;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsInfoBottom .goodsInfoBottomRight {\n  font-size: .16rem;\n  color: #F61027;\n  font-weight: 800;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsInfoBottom .goodsInfoBottomRight .goodsPrice {\n  font-size: .36rem;\n  line-height: .36rem;\n  font-weight: 800;\n  color: #F61027;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsInfoBottom .goodsInfoBottomRight .promNum {\n  font-size: .2rem;\n  line-height: .2rem;\n  margin-left: .06rem;\n  color: #999;\n  font-weight: 200;\n}\n\n#chat .chatMsgItems .chatMsgItem .goodsLink .goodsInfo .goodsInfoBottom .goodsInfoBottomLeft {\n  width: 1.6rem;\n  height: .5rem;\n  text-align: center;\n  line-height: .46rem;\n  font-size: .22rem;\n  color: #F61027;\n  font-weight: 600;\n  border: .02rem solid #F61027;\n  border-radius: .25rem;\n}\n\n#chat .chatMsgItems .chatMsgItem .send {\n  justify-content: end;\n  flex-direction: row-reverse;\n}\n\n#chat .chatMsgItems .chatMsgItem .send .chatMsgBox {\n  background-color: #F65565;\n  border-top-right-radius: 0;\n  border-top-left-radius: .2rem;\n  color: #fff;\n}\n\n#chat .msgSendBox {\n  width: 100%;\n  max-width: 20rem;\n  position: fixed;\n  bottom: 0.266667rem;\n  background-color: #fff;\n  padding: .15rem .2rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n#chat .msgSendBox textarea {\n  resize: none;\n  background-color: #f6f6f6;\n  border-radius: 0.106667rem;\n  width: 8.666667rem;\n  max-width: 13.333333rem;\n  height: 0.96rem;\n  font-size: 0.373333rem;\n}\n\n#chat .msgSendBox #images {\n  display: none;\n}\n\n#chat .msgSendBox .send {\n  width: 1rem;\n  padding-left: .2rem;\n  display: flex;\n  justify-content: center;\n}\n\n#chat .msgSendBox .send img {\n  height: .66rem;\n  width: .66rem;\n}\n\n#chat .msgSendBox .send button {\n  width: .9rem;\n  height: .7rem;\n  background-color: #F61027;\n  color: #fff;\n  border-radius: .1rem;\n  font-size: .28rem;\n}\n\n#chat .msgSendBox .send .fade-enter-active,\n#chat .msgSendBox .send .fade-leave-active {\n  transition: opacity .1s;\n}\n\n#chat .msgSendBox .send .fade-enter,\n#chat .msgSendBox .send .fade-leave-to {\n  opacity: 0;\n}\n\n.sendImg {\n  max-width: 2.666667rem;\n  max-height: 3.733333rem;\n}"],"sourceRoot":""}]);

